
































import Vue from 'vue';
import Swap from '@/components/swap/Swap.vue';
import { mapState } from 'vuex';

export default Vue.extend({
  components: {
    Swap,
  },
  data: () => ({
    label: {
      title: '交易',
    },
    input: 0,
    output: 0,
    snackbar: false,
    snackbarText: '',
  }),
  created() {
    this.input = parseInt(this.$route.params.input);
    this.output = parseInt(this.$route.params.output);
  },
  computed: {
    ...mapState({
      accounts: (state: any) => state.wallet.accounts,
    })
  },
  methods: {
    copy() {
      navigator.clipboard.writeText(this.accounts[0].toString());
      this.snackbarText = this.$i18n.t('SwapAddressSnackbarText').toString();
      this.snackbar = true;
    }
  }
});
