









































import Vue from 'vue';
import { mapState } from 'vuex';

import Constants from '@/constants';

export default Vue.extend({
  name: 'SelectDialog',
  props: {
    exclude: {
      type: String,
    },
    selected: {
      type: String,
    }
  },
  data() {
    return {
      select: 0,
      dialog: false,
      tokenList: [''],
      balanceList: [''],
      addressList: [''],
      search: '',
    };
  },
  computed: {
    ...mapState({
      chainId: (state: any) => state.wallet.chainId,
      accounts: (state: any) => state.wallet.accounts,
      balances: (state: any) => state.swap.balances,
      filteredItems: function() {
        return this.tokenList.filter((item: string) => {
          return item.match(this.search.toUpperCase());
        });
      },
    }),
  },
  methods: {
    submit(token: string) {
      console.warn(token);
      this.$emit('select', token);
      this.dialog = false;
    },
  },
  watch: {
    dialog: function() {
      this.tokenList = [];

      for (const token of Object.keys(Constants[this.chainId])) {
        if (token !== this.selected && token !== this.exclude) {
          this.tokenList.push(token);
        }
      }
    }
  },
});
