




























































































































































































































































































































































































































import Vue from 'vue';
import { mapActions, mapMutations, mapState } from 'vuex';

// import Button from '@/components/framework/Button.vue';
import SelectTokenDialog from '@/components/swap/SelectDialog.vue';
import Constants from '@/constants';
import i18n from '@/locale';
import { compare, fixTo8, fromFloat2Integer, fromInteger2Float } from '@/libs';

export default Vue.extend({
  name: 'Transaction.vue',
  components: {
    // Button,
    SelectTokenDialog,
  },
  data: () => ({
    tab: 0,
    txTypes: ['Market'],

    path: [''],

    tokenList: [''],
    // balanceList: [''],
    inputPairList: [''],
    outputPairList: [''],

    inputTokenDesc: '',
    inputTokenIndex: 0,
    inputBalance: '',
    inputValue: '',
    inputAllowance: '0x0',

    inputErrorMessage: '',
    inputError: false,

    outputTokenDesc: '',
    outputTokenIndex: 1,
    outputBalance: '',
    outputValue: '',
    outputAllowance: '0x0',

    openPanel: -1,
    slipPoints: [
      {
        label: '0.1',
      },
      {
        label: '0.5',
      },
      {
        label: '1',
      },
    ],
    disableChip: false,

    txDue: '',
    slipPointChipIndex: 0,
    slipPointCustom: '',

    revertRate: false,

    actionDisable: true,
    working: false,

    loaded: false,

    snackbar: false,
    snackbarTx: '',
    errSnackbar: false,
    errSnackbarText: '',

    aggrHeaders: [
      {
        text: i18n.t('SwapAggrDex'),
        value: 'dex',
        sortable: false,
        align: 'start',
      },
      {
        text: i18n.t('SwagAggrMinIncome'),
        value: 'minIncome',
        sortable: false,
      },
      {
        text: i18n.t('SwagAggrAction'),
        sortable: false,
        value: 'actions',
      },
    ],
    aggrItems: [] as any,

    rules: {
      positiveNumber: (value: string = '') => {
        // positive float or positive integer
        const localValue: string = value.toString();
        if (localValue.match(/^[1-9]\d*\.\d*|0\.\d*[1-9]\d*$/) || localValue.match(/^[1-9]\d*$/) || localValue == '') {
          return true;
        } else {
          return i18n.t('SwapInputWarning');
        }
      },
      positiveInteger: (value: string = '') => {
        const localValue: string = value.toString();
        if (localValue.match(/^[1-9]\d*$/) || localValue === '') {
          return true;
        } else {
          return i18n.t('SwapTxDueWarning');
        }
      },
      lengthLimit: (value: string = '') => {
        const localValue: string = value.toString();
        if (localValue.length < 25) {
          return true;
        } else {
          return i18n.t('SwapInputExceedLengthWarning');
        }
      },
    },
  }),

  watch: {
    inputValue: async function (val) {
      if (
        this.rules.lengthLimit(val) &&
        this.rules.positiveNumber(val) &&
        parseFloat(this.inputValue) < parseFloat(this.balanceIn) &&
        parseFloat(this.inputValue) > 0
      ) {
        await this.setAmountIn(val);
        await this.getAmountOut();
        await this.getTxFee();
        await this.getMinIncome();
        await this.aggrMinIncome();
      } else {
        await this.resetAmountOut();
      }
    },
  },

  computed: {
    ...mapState(
      'wallet', [
      'chainId',
      'accounts',
    ],
    ),
    ...mapState(
      'swap', [
      'slipPoint',
      'deadline',
      'tokenIn',
      'tokenOut',
      'balanceIn',
      'balanceOut',
      'amountIn',
      'amountOut',
      'amountOutMin',
      'exchangeRate',
      'txFee',
      'isApproved',

      // TODO: EXPERIMENTAL
      // 'tokenGraph',
    ],
    ),
  },

  methods: {
    ...mapActions({
      connect: 'wallet/connect',
      connectWallet: 'wallet/connectWallet',

      balanceOf: 'erc20/balanceOf',
      approve: 'erc20/approve',
      allowance: 'erc20/allowance',

      getAmountsOut: 'mdexRouter/getAmountsOut',
      getAmountOutMin: 'mdexRouter/getAmountOutMin',
      swap: 'swap/swap',
      getApprove: 'swap/getApprove',
      getExchangeRate: 'swap/getExchangeRate',
      fetchBalances: 'swap/fetchBalances',
      getTxFee: 'swap/getTxFee',
      getMinIncome: 'swap/getMinIncome',
      getAmountOut: 'swap/getAmountOut',
      getBalance: 'swap/getBalance',

      // TODO: EXPERIMENTAL
      // initGraph: 'swap/initGraph',
      // shortestPath: 'swap/shortestPath',

      // TODO: Refactor in the future
      pippiGetAmountsOut: 'mdexRouter/pippiGetAmountsOut',
      dogeGetAmountsOut: 'mdexRouter/dogeGetAmountsOut',
      lavaGetAmountsOut: 'mdexRouter/lavaGetAmountsOut',
      BXHGetAmountsOut: 'mdexRouter/BXHGetAmountsOut',
    }),
    ...mapMutations({
      setTokenIn: 'swap/setTokenIn',
      setTokenOut: 'swap/setTokenOut',
      setAmountIn: 'swap/setAmountIn',
      resetAmountOut: 'swap/resetAmountOut',
    }),
    getPairList(token: string) {
      const pairList = [];
      for (const peer of Object.keys(Constants['pairs'][token])) {
        pairList.push(peer);
      }
      return pairList;
    },
    async makeApprove() {
      this.working = true;

      this.approve({
        contractAddress: Constants[this.chainId][this.tokenIn].address,
        spenderAddress: Constants['swap'][this.chainId],
        amount: Constants.allowance,
      })
        .then(async (response) => {
          console.log(`response: ${JSON.stringify(response)}`);
          const receipt = await response.wait(1);
          await this.getApprove();
          this.working = false;
          console.log(`receipt: ${JSON.stringify(receipt)}`);
        }, (err) => {
          console.log(err);
        });
    },
    async makeSwap() {
      this.working = true;

      this.swap()
        .then(async (response) => {
          console.log(`response: ${JSON.stringify(response)}`);
          await response.wait(1)
            .then(async (receipt: any) => {
              await this.getBalance();

              this.snackbarTx = response.hash;
              this.snackbar = true;

              this.inputValue = '';
              this.setAmountIn('');
              this.resetAmountOut();
              this.fetchBalances({ reverse: this.revertRate });

              this.working = false;

              console.log(`receipt: ${JSON.stringify(receipt)}`);
            }, (err: any) => {
              if (err) {
                this.errSnackbarText = `${this.$i18n.t('SwapErrSnackbar')
                  .toString()} ${err.toString()
                    .slice(0, 60)}...`;
                this.errSnackbar = true;
                this.working = false;
              }
              console.error('Swap Error: ', err);
            });
        }, (err) => {
          if (err.data) {
            if (err.data.code === 3) {
              this.errSnackbarText = this.$i18n.t('SwapErrSnackbar')
                .toString() + err.data.message;
              this.errSnackbar = true;
              this.working = false;
            }
          } else if (err.code === 4001) {
            this.errSnackbarText = this.$i18n.t('SwapRefuseSnackbar')
              .toString();
            this.errSnackbar = true;
            this.working = true;
          } else {
            this.errSnackbarText = err;
            this.errSnackbar = true;
            this.working = true;
          }
          console.error('SwapError: ', err);
        });
    },

    swapInputOutput() {
      const temp = this.tokenIn;

      this.setTokenIn(this.tokenOut);
      this.inputValue = '';
      this.setAmountIn('');

      this.setTokenOut(temp);
      this.getBalance();
      this.getApprove();
      this.getExchangeRate({ reverse: this.revertRate });
      this.aggrMinIncome();
    },

    async max() {
      if (parseFloat(this.balanceIn) === 0) return;
      this.working = true;
      this.inputValue = this.balanceIn;
      await this.setAmountIn(this.balanceIn);
      await this.getAmountOut();
      await this.getTxFee();
      await this.getMinIncome();
      await this.aggrMinIncome();
      this.working = false;
    },

    ruleBalance() {
      let balanceNotEnough = false;
      if (this.inputValue !== '') {
        balanceNotEnough = compare(
          fromFloat2Integer(
            this.inputValue,
            Constants[this.chainId][this.tokenIn].decimal,
          ),
          fromFloat2Integer(
            this.balanceIn,
            Constants[this.chainId][this.tokenIn].decimal,
          ),
        ) <= 0;
      }
      console.log('cmpResult: ', balanceNotEnough);
      if (this.inputValue == '' || balanceNotEnough && parseFloat(this.inputValue) <= Constants['limit'][this.tokenIn]) {
        this.inputError = false;
        return true;
      } else {
        if (parseFloat(this.inputValue) > Constants['limit'][this.tokenIn]) {
          this.inputError = true;
          return i18n.t('SwapInsufficientLiquidity');
        } else {
          this.inputError = true;
          return i18n.t('SwapInsufficientFundWarning');
        }
      }
    },

    async onInputChange(token: string) {
      try {
        await this.setTokenIn(token);
        this.getBalance();
        this.getExchangeRate({ reverse: this.revertRate });
        this.getPath();
        this.getApprove();
        this.setAmountIn('');
        this.resetAmountOut();
        this.inputValue = '';
        this.fetchBalances({ reverse: this.revertRate });
        await this.aggrMinIncome();
      } catch (e) {
        console.error('onInputChange(): ', e);
      }
    },
    async onOutputChange(token: string) {
      await this.setTokenOut(token);
      this.getBalance();
      this.getExchangeRate({ reverse: this.revertRate });
      this.getPath();
      this.setAmountIn('');
      this.resetAmountOut();
      this.fetchBalances({ reverse: this.revertRate });
      await this.aggrMinIncome();
    },

    getPath() {
      this.path = Constants['pairs'][this.tokenIn][this.tokenOut].path[0];
    },

    revert() {
      this.revertRate = !this.revertRate;
      this.getExchangeRate({ reverse: this.revertRate });
    },

    // TODO: Use generated path in the future
    async aggrMinIncome() {
      this.aggrItems = [];

      if (parseFloat(this.inputValue) > 0) {
        try {
          // MDEX
          const amountOut = (await this.getAmountsOut({
            contractAddress: '0xed7d5f38c79115ca12fe6c0041abb22f0a06c300',
            amountIn: fromFloat2Integer(
              this.inputValue,
              Constants[this.chainId][this.tokenIn].decimal,
            ),
            path: this.pathName2Addr(Constants['pairs'][this.tokenIn][this.tokenOut].path[0]),
          })).split(',').slice(-1)[0];

          const minIncome = fixTo8(
            fromInteger2Float(
              await this.getAmountOutMin(
                {
                  amountOut,
                  slipPoint: this.slipPoint,
                },
              ),
              Constants[this.chainId][this.tokenOut].decimal,
            ),
          );

          this.aggrItems.push({
            dex: 'MDEX',
            minIncome,
            url: 'https://ht.mdex.com/#/swap',
            contract: '0xed7d5f38c79115ca12fe6c0041abb22f0a06c300',
            redirect: false,
          });
        } catch (error) {
          console.log('MDEX ERROR:', error);
          this.aggrItems[0].minIncome = '...';
        }

        // PIPPI
        try {
          let data = {
            dex: 'PIPPI',
            minIncome: '...',
            url: 'https://swap.pippi.finance/#/swap',
            contract: '0xbe4ab2603140f134869cb32ab4bc56d762ae900b',
            redirect: true,
          };

          const amountOut = (await this.pippiGetAmountsOut({
            contractAddress: data.contract,
            amountIn: fromFloat2Integer(
              this.inputValue,
              Constants[this.chainId][this.tokenIn].decimal,
            ),
            path: [
              Constants[this.chainId][this.tokenIn].address,
              Constants[this.chainId][this.tokenOut].address,
            ],
          })).split(',').slice(-1)[0];

          data.minIncome = fixTo8(
            fromInteger2Float(
              await this.getAmountOutMin(
                {
                  amountOut,
                  slipPoint: this.slipPoint,
                },
              ),
              Constants[this.chainId][this.tokenOut].decimal,
            ),
          );
          this.aggrItems.push(data);
        } catch (error) {
          console.log('PPIPI ERROR:', error);
        }

        // DOGE
        try {
          let data = {
            dex: 'DOGE',
            minIncome: '...',
            url: 'https://dogeswap.com/#/swap',
            contract: '0x539A9Fbb81D1D2DC805c698B55C8DF81cbA6b350',
            redirect: true,
          };

          const amountOut = (await this.dogeGetAmountsOut({
            contractAddress: data.contract,
            amountIn: fromFloat2Integer(
              this.inputValue,
              Constants[this.chainId][this.tokenIn].decimal,
            ),
            path: [
              Constants[this.chainId][this.tokenIn].address,
              Constants[this.chainId][this.tokenOut].address,
            ],
          })).split(',').slice(-1)[0];

          data.minIncome = fixTo8(
            fromInteger2Float(
              await this.getAmountOutMin({
                amountOut,
                slipPoint: this.slipPoint,
              }),
              Constants[this.chainId][this.tokenOut].decimal,
            ),
          );
          this.aggrItems.push(data);
        } catch (error) {
          console.log('DOGE ERROR:', error);
        }

        // LAVA
        try {
          let data = {
            dex: 'LAVA',
            minIncome: '...',
            url: 'https://exchange.lavaswap.com/#/swap',
            contract: '0xe38623B265B5Acc9F35E696381769E556Ed932f9',
            redirect: true,
          };

          const amountOut = (await this.lavaGetAmountsOut({
            contractAddress: data.contract,
            amountIn: fromFloat2Integer(
              this.inputValue,
              Constants[this.chainId][this.tokenIn].decimal,
            ),
            path: [
              Constants[this.chainId][this.tokenIn].address,
              Constants[this.chainId][this.tokenOut].address,
            ],
          })).split(',').slice(-1)[0];

          data.minIncome = fixTo8(
            fromInteger2Float(
              await this.getAmountOutMin({
                amountOut,
                slipPoint: this.slipPoint,
              }),
              Constants[this.chainId][this.tokenOut].decimal,
            ),
          );
          this.aggrItems.push(data);
        } catch (error) {
          console.log('LAVA ERROR:', error);
        }

        // BXH
        try {
          let data = {
            dex: 'BXH',
            minIncome: '...',
            url: 'https://www.bxh.com/#/swap',
            contract: '0x00eFB96dBFE641246E961b472C0C3fC472f6a694',
            redirect: true,
          };

          const amountOut = (await this.BXHGetAmountsOut({
            contractAddress: data.contract,
            amountIn: fromFloat2Integer(
              this.inputValue,
              Constants[this.chainId][this.tokenIn].decimal,
            ),
            path: [
              Constants[this.chainId][this.tokenIn].address,
              Constants[this.chainId][this.tokenOut].address,
            ],
          })).split(',').slice(-1)[0];

          data.minIncome = fixTo8(
            fromInteger2Float(
              await this.getAmountOutMin({
                amountOut,
                slipPoint: this.slipPoint,
              }),
              Constants[this.chainId][this.tokenOut].decimal,
            ),
          );
          this.aggrItems.push(data);
        } catch (error) {
          console.log('BXH ERROR:', error);
        }

        // Sort best price
        this.aggrItems.sort((a: any, b: any) => {
          return b.minIncome - a.minIncome;
        });
      }
    },
    pathName2Addr(path: Array<string>): Array<string> {
      let result: Array<string> = [];
      for (const tokenName of path) {
        result.push(Constants[this.chainId][tokenName].address);
      }
      return result;
    },
  },

  async mounted() {
    await this.connect();
    await this.connectWallet();

    // initialize token list
    this.tokenList = [];
    for (const coin of Object.keys(Constants[this.chainId])) {
      this.tokenList.push(coin);
    }

    this.setTokenIn(this.$route.query.input || this.tokenList[0]);
    this.setTokenOut(this.$route.query.output || this.tokenList[1]);

    this.getApprove();
    this.getPath();
    await this.getBalance();
    await this.getExchangeRate({ reverse: false });
    await this.fetchBalances({ reverse: this.revertRate });
    await this.aggrMinIncome();

    this.loaded = true;

    // TODO: Experimental Content
    // await this.initGraph();
    // setTimeout(async () => {
    //   console.warn(this.tokenGraph.getEdgeAttribute('VNLA', 'BEE', 'weight'));
    //   console.log(await this.shortestPath({ amountIn: 'VNLA', amountOut: 'BEE' }));
    // }, 5000);
  },
});
